@font-face {
    font-family: 'ONE-Mobile-POP';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'omyu_pretty';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-01@1.0/omyu_pretty.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}
.weatherDiv{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* overflow: hidden; */
    background-color: #ddd;
    font-family: 'omyu_pretty';
    font-size: 20px;
}
.weatherDiv .wea-wrapper{
    width: 500px;
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: white;
    overflow: hidden;
    position: relative;
}
.weatherDiv .search-div{
    /* position: absolute; */
    width: 100%;
    height: 150px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.weatherDiv .main-Text{
    font-family: 'ONE-Mobile-POP';
    font-size: 20px;
    /* margin-bottom: 10px; */

}
.weatherDiv .recent-searches{
    /* position: absolute; */
    font-size: 16px;
    padding: 0px 45px 10px;
    border-bottom: 1px solid rgb(207, 207, 207);
}
.weatherDiv .favorites-list{
    font-size: 16px;
    padding: 10px 45px;
}
.favorites-list .list-btn, .recent-searches .list-btn{
    background: white;
    background-color: rgb(223, 222, 222);
    border: none;
    color: #222;
    padding: 3px 10px;
    font-size: 14px;
    border-radius: 10px 0px 0px 10px;
    /* margin-right: 10px; */
    font-family: 'omyu_pretty';
}
.favorites-list .font-semibold,.recent-searches .font-semibold{
    /* margin-bottom: 10px; */
}
.weatherDiv .all-delete{
    background-color: white;
    border:1px solid rgb(223, 222, 222);
    /* border: none; */
    font-family: 'omyu_pretty';
    padding: 3px 10px;
    border-radius: 15px;
}
.favorites-list .remove-btn, .recent-searches .remove-btn{
    background-color: rgb(223, 222, 222);
    /* color: white; */
    border: none;
    height: 21px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0px 10px 10px 0px;
}
.favorites-list .list-wrap, .recent-searches .list-wrap{
    display: flex;
    gap: 10px;
    overflow-x: auto;
    padding: 5px 0px;
}
.weatherDiv .list-flex-div{
    display: flex;
    align-items: center;
    gap: 3px;
}
.weatherDiv .text-and-delete{
    display: flex;
    align-items: center;
    /* justify-content: center; */
    gap: 10px;
}
.wea-wrapper > .text-red-500{
    text-align: center;
    margin-bottom: 30px;
}
@media screen and (max-width : 768px) {
    .weatherDiv{
        font-size: 16px;
    }
}